<template>
    <div class="role">
        <el-row :gutter="20" class="functionalArea">
            <el-col :span="6">
                <el-button-group>
                    <el-button type="primary" icon="el-icon-plus" @click="addBtnFn"></el-button>
                    <el-button type="primary" icon="el-icon-share"></el-button>
                    <el-button type="primary" icon="el-icon-delete" @click="clearBtnFn"></el-button>
                </el-button-group>
            </el-col>
            <el-col :span="6" :offset="12">
                <el-input placeholder="请输入内容" v-model="searchInput" clearable>
                    <template slot="append">
                        <el-button type="success" plain>搜索</el-button>
                    </template>
                </el-input>
            </el-col>
        </el-row>

        <el-table
                ref="multipleTable"
                :data="tableData"
                style="width: 100%"
                :header-cell-style="headerCellStyle"
                :cell-style="cellStyle"
                @select="handleSelectionChange"
                @select-all="selectAllFn"
        >
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column
                    label="序号"
                    width="60">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.index }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="流程环节"
                    width="200">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.step }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="开始"
                    width="100">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.start_msg }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="通过"
                    width="100">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.pass_msg }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="未通过"
                    width="100">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.unpass_msg }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="操作"
                    min-width="240">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="添加短信" :visible.sync="addFormVisible" width="800px">
            <el-form :model="form">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-form-item label="流程名称" :label-width="formLabelWidth">
                            <el-select v-model="form.step" placeholder="请选择流程" style="width:100%;">
                                <el-option v-for="(item, index) in processList" :key="index" :label="item.name" :value="item.name"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="流程开始" :label-width="formLabelWidth">
                            <el-select v-model="form.start_msg" placeholder="请选择流程节点" style="width:100%;">
                                <el-option v-for="(item, index) in isList" :key="index" :label="item.label" :value="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="流程通过" :label-width="formLabelWidth">
                            <el-select v-model="form.pass_msg" placeholder="请选择流程节点" style="width:100%;">
                                <el-option v-for="(item, index) in isList" :key="index" :label="item.label" :value="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="流程未通过" :label-width="formLabelWidth">
                            <el-select v-model="form.unpass_msg" placeholder="请选择流程节点" style="width:100%;">
                                <el-option v-for="(item, index) in isList" :key="index" :label="item.label" :value="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="addSureFn">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="编辑短信" :visible.sync="fixFormVisible" width="800px">
            <el-form :model="form1">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-form-item label="流程名称" :label-width="formLabelWidth">
                            <el-select v-model="form1.step" placeholder="请选择流程" style="width:100%;">
                                <el-option v-for="(item, index) in processList" :key="index" :label="item.name" :value="item.name"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="流程开始" :label-width="formLabelWidth">
                            <el-select v-model="form1.start_msg" placeholder="请选择流程节点" style="width:100%;">
                                <el-option v-for="(item, index) in isList" :key="index" :label="item.label" :value="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="流程通过" :label-width="formLabelWidth">
                            <el-select v-model="form1.pass_msg" placeholder="请选择流程节点" style="width:100%;">
                                <el-option v-for="(item, index) in isList" :key="index" :label="item.label" :value="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="流程未通过" :label-width="formLabelWidth">
                            <el-select v-model="form1.unpass_msg" placeholder="请选择流程节点" style="width:100%;">
                                <el-option v-for="(item, index) in isList" :key="index" :label="item.label" :value="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="fixFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="fixSureFn">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

    export default {
        name: 'ATheme',
        data () {
            return {
                searchInput: '',
                tableData: [],
                jurisdictionData: [],

                themeList: [],
                processList: [
                    {
                        name: "接件"
                    },
                    {
                        name: "经办人"
                    },
                    {
                        name: "科室负责人"
                    },
                    {
                        name: "探勘中心"
                    },
                    {
                        name: "分管领导"
                    },
                    {
                        name: "发证"
                    },
                ],
                isList: [
                    {
                        label: '是',
                        val: 'Y'
                    },
                    {
                        label: '否',
                        val: 'N'
                    }
                ],

                addFormVisible: false,
                formLabelWidth: '120px',
                form: {
                    step: '',
                    start_msg: '',
                    pass_msg: '',
                    unpass_msg: ''
                },
                fixFormVisible: false,
                form1: {
                    step: '',
                    start_msg: '',
                    pass_msg: '',
                    unpass_msg: '',
                    id: ''
                },

                selectIds: [],

                notifyPromise:Promise.resolve()
            }
        },
        watch:{
        },
        created:function () {
            this.getMsgFn()
        },
        methods:{
            addBtnFn(){
                this.addFormVisible=true;
                this.form.start_msg='Y'
                this.form.pass_msg='Y'
                this.form.unpass_msg='Y'
            },
            addSureFn() {
                let temp={
                    step: this.form.step,
                    start_msg: this.form.start_msg,
                    pass_msg: this.form.pass_msg,
                    unpass_msg: this.form.unpass_msg,
                    user_id: sessionStorage.user_id
                };
                if(temp.step!=''){
                    this.addFn(temp)
                }else{
                    this.notify('未选择流程环节');
                }
            },
            //通知，解决element-ui，同时调用notify时，通知重叠的问题
            notify(msg) {
                this.notifyPromise = this.notifyPromise.then(this.$nextTick).then(()=>{
                    this.$notify({
                        title: "提示",
                        message: msg,
                        type: 'warning',
                        dangerouslyUseHTMLString: true,
                        duration: 5000,
                        position: "top-right"
                    });
                })
            },
            handleEdit(index, row) {
                console.log(index, row);
                this.fixFormVisible=true
                this.form1.step=row.step;
                this.form1.start_msg=row.start_msg;
                this.form1.pass_msg=row.pass_msg;
                this.form1.unpass_msg=row.unpass_msg;

                this.form1.id=row.id
            },
            fixSureFn() {
                let temp={
                    id: this.form1.id,
                    step: this.form1.step,
                    start_msg: this.form1.start_msg,
                    pass_msg: this.form1.pass_msg,
                    unpass_msg: this.form1.unpass_msg,
                    user_id: sessionStorage.user_id
                };
                if(temp.step!=''){
                    this.fixFn(temp)
                }else{
                    this.notify('未选择流程环节');
                }
            },
            clearBtnFn() {
                if(this.selectIds.length){
                    this.$confirm('确定删除所选短信', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        let params={
                            id_list: this.selectIds,
                            user_id: sessionStorage.user_id
                        };
                        this.deleteSingleFn(params)
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                    });

                }else{
                    this.$message({
                        type: 'warning',
                        message: '未选择短信!'
                    });
                }
            },
            handleDelete(index, row) {
                console.log(row);
                this.$confirm('确定删除短信：'+row.step, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let params={
                        id_list: [row.id],
                        user_id: sessionStorage.user_id
                    };
                    this.deleteSingleFn(params)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            async getThemeFn () {
                try {
                    let params = {}
                    let res = await this.$api.unsplash.getSubjects(params)
                    console.log(res)
                    this.themeList=[]
                    if(res.errno=='0'){
                        for(let i=0;i<res.data.subjects.length;i++){
                            this.themeList.push({
                                label: res.data.subjects[i].name,
                                val: res.data.subjects[i].id
                            })
                        }
                        // this.themeList.push({
                        //     label: '所有',
                        //     val: -1
                        // })
                        //
                        // this.selectThemeId=this.themeList[0].val
                        // this.selectThemeName=this.themeList[0].label
                        // this.getUserFn(this.selectThemeName);
                    }else{
                        this.$alert('服务器错误', '警告', {
                            confirmButtonText: '确定',
                            callback: action => {
                            }
                        });
                    }
                } catch (e) {
                    console.log(e)
                }
            },
            async getMsgFn () {
                let params={};
                try {
                    let res = await this.$api.unsplash.getMsg(params)
                    console.log(res)
                    if(res.errno=='0'){
                        this.tableData=this.addSequence(res.data.msgs);
                    }else{
                        this.$message({
                            type: 'warning',
                            message: '获取短信失败'
                        });
                    }
                } catch (e) {
                    console.log(e)
                }
            },
            async addFn (params) {
                try {
                    let res = await this.$api.unsplash.postAddMsg(params)
                    if(res.errno=='0'){
                        this.addFormVisible=false
                        this.$message({
                            type: 'success',
                            message: '增加短信成功'
                        });
                        this.getMsgFn()
                    }else{
                        this.notify(res.errmsg);
                    }
                } catch (e) {
                    console.log(e)
                }
            },
            async fixFn (params) {
                try {
                    let res = await this.$api.unsplash.postfixMsg(params)
                    if(res.errno=='0'){
                        this.fixFormVisible = false
                        this.$message({
                            type: 'success',
                            message: '修改短信成功'
                        });
                        this.getMsgFn()
                    }else{
                        this.$message({
                            type: 'warning',
                            message: '修改短信失败'
                        });
                    }
                } catch (e) {
                    console.log(e)
                }
            },
            async deleteSingleFn (params) {
                try {
                    let res = await this.$api.unsplash.postClearMsg(params)
                    if(res.errno=='0'){
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                        this.selectIds= []
                        this.getMsgFn()
                    }else{
                        this.myAlert('服务器错误','警告',this)
                    }
                } catch (e) {
                    console.log(e)
                }
            },
            handleSelectionChange(selection, row) { // 单击
                console.log(selection);
                this.selectIds =[];
                for(let i=0;i<selection.length;i++){
                    this.selectIds.push(selection[i].id);
                }
            },
            selectAllFn(selection){ // 全选
                console.log(selection);
                this.selectIds =[];
                for(let i=0;i<selection.length;i++){
                    this.selectIds.push(selection[i].id);
                }
            },
            headerCellStyle({ row, column, rowIndex, columnIndex }){
                if(rowIndex>-1){
                    return {
                        'text-align': 'center',
                    };
                }
            },
            cellStyle ({ row, column, rowIndex, columnIndex }) {
                if (columnIndex >= 0) {
                    return {
                        'text-align': 'center',
                    };
                }
            },
            myAlert(txt,title,self){
                self.$alert(txt, title, {
                    confirmButtonText: '确定',
                    callback: action => {
                    }
                });
            },
            addSequence(temp){
                for(let i=0;i<temp.length;i++){
                    temp[i].index=i+1;
                }
                return temp;
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
    .role{
        .functionalArea{
            height: 80px;
        }
        .headerCellStyle{
            text-align: center;
        }
        .el-dialog__body {
            padding: 30px 60px 30px 20px;
        }
    }
</style>



